.gameboard {
  display: grid;
  height: 100%;
  overflow-y: auto;
  padding-bottom: 6rem;
}

.gameboard ol {
  list-style: none;
  padding: 0;
  --hue: 1;
  --unit: 1rem;
}

.gameboard li {
  --height: 6rem;
  position: relative;
  display: block;
  background-color: hsl(calc(var(--hue) * 360 / 10), 70%, 30%);
  border-color: hsl(calc(var(--hue) * 360 / 10), 70%, 30%);
  padding: 0.75rem;
  margin: 2rem 1rem;
  border-radius: 0.2rem;
}

.gameboard li.gray-tiles {
  background-color: rgba(75, 75, 75, 0.75);
  border-color: rgba(75, 75, 75, 0.75);
}

.gameboard li .pop-text {
  text-shadow:
    -1px 0 0 black,
    1px 0 0 black,
    0 -1px 0 black,
    0 1px 0 black;
}

.gameboard li::before {
  content: '';
  background-color: inherit;
  position: absolute;
  display: block;
  width: var(--unit);
  height: var(--unit);
  top: 100%;
  left: calc(50% - (var(--unit) / 2));
}

.gameboard li::after {
  content: '';
  position: absolute;
  display: block;
  top: calc(100% + var(--unit));
  left: calc(50% - (var(--unit)));
  border: var(--unit) solid transparent;
  border-top-color: inherit;
}

.gameboard li:last-child::before,
.gameboard li:last-child::after {
  content: none;
}

.gameboard .hue0 {
  --hue: 0;
}

.gameboard .hue1 {
  --hue: 3;
}

.gameboard .hue2 {
  --hue: 6;
}

.gameboard .hue3 {
  --hue: 9;
}

.gameboard .hue4 {
  --hue: 2;
}

.gameboard .hue5 {
  --hue: 5;
}

.gameboard .hue6 {
  --hue: 8;
}

.gameboard .hue7 {
  --hue: 1;
}

.gameboard .hue8 {
  --hue: 4;
}

.gameboard .hue9 {
  --hue: 7;
}

.gameboard .hue10 {
  --hue: 10;
}

@media (min-width: 820px) and (max-width: 1019px) {
  .gameboard li {
    margin: 1rem;
    width: calc(50% - 2rem);
    float: left;
    min-height: 11rem;
  }

  .gameboard li:nth-child(4n + 3),
  .gameboard li:nth-child(4n + 4) {
    float: right;
  }

  .gameboard li:nth-child(4n + 1)::before {
    top: calc(var(--height) / 2 + var(--unit) / 2);
    left: 100%;
  }

  .gameboard li:nth-child(4n + 1)::after {
    top: calc(var(--height) / 2);
    left: calc(100% + (var(--unit)));
    border: var(--unit) solid transparent;
    border-left-color: inherit;
  }

  .gameboard li:nth-child(4n + 3)::before {
    top: calc(var(--height) / 2 + var(--unit) / 2);
    left: -1rem;
  }

  .gameboard li:nth-child(4n + 3)::after {
    top: calc(var(--height) / 2);
    left: -3rem;
    border: var(--unit) solid transparent;
    border-right-color: inherit;
  }
}

